import {createStore} from 'vuex'
const store = createStore({
    state() {
        return {
            // Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú Ï†ëÍ∑ºÍ∞ÄÎä•
            todos: [],
            name:''
        }
    },
    mutations: {
        // state Î≥ÄÏàòÎäî Ïô∏Î∂Ä Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú ÏàòÏ†ï Î∂àÍ∞ÄÎä•ÌïòÎØÄÎ°ú
        // mutation ÎÇ¥ Ï†ïÏùòÎêú Ìï®ÏàòÎ•º Ïù¥Ïö©Ìï¥Ïïº ÏÉÅÌÉúÏùò Î≥ÄÍ≤Ω Í∞ÄÎä•ÌïòÎã§
        addTodo(state, content) {
            state.todos.push(content)
        },
        delTodo(state, idx){
            state.todos.splice(idx,1)
        },
        addName(state, name){
          state.name = name
        },
        changeCheck(state,obj){
            state.todos[obj.idx].completed = obj.data;
        }
    }
})

export default store